@import '../../../styles/imports/shared-imports';
@import '../../collapsible-card/css/global';

.personal-document-listing {
    @include pnp-svg-icon-classes;
    @include pnp-typeface(text-14, no-margin);

    &__list {
        @include pnp-list-reset;
    }

    &__footer {
        display: flex;
        margin-top: 18px;

        @include pnp-media-breakpoint-up(md) {
            justify-content: flex-end;
        }
    }

    &__all-documents {
        @include pnp-link-text;
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }

        .svg-icon {
            margin-right: 4px;
        }
    }
}

.personal-document-listing-item {
    @include pnp-typeface(text-12, no-margin);
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 20px 0;
    color: $grey-80;

    @include pnp-media-breakpoint-up(md) {
        flex-direction: row;
        margin: 10px 0;
    }

    &__icon {
        position: absolute;
        width: 16px;

        @include pnp-media-breakpoint-up(md) {
            position: static;
            margin-right: 16px;
        }
    }

    &__title {
        @include pnp-link-text;
        @include pnp-typeface(text-14, no-margin);
        display: block;
        margin-bottom: 10px;
        margin-left: 20px;
        text-decoration: none;

        @include pnp-media-breakpoint-up(md) {
            flex: 3 1 0;
            margin: 0 25px 0 0;
        }

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    &__last-modified {
        @include pnp-media-breakpoint-up(md) {
            display: flex;
            flex: 3 1 0;
            flex-direction: column;
            margin-right: 25px;
        }

        &-date {
            display: inline-block;
            margin-right: 6px;
        }

        &-by {
            display: inline-block;
        }
    }

    &__size {
        @include pnp-media-breakpoint-up(md) {
            flex: 1 1 0;
            min-width: 50px;
            text-align: end;
        }
    }
}

.personal-document-listing-message {
    display: flex;
    align-items: center;
    justify-content: center;

    &__body {
        position: relative;
        display: flex;
        align-items: center;
        min-width: 300px;
        max-width: 350px;
        min-height: 95px;
        margin-left: 50px;
        padding: 10px 25px 10px 55px;
        background: $grey-10;
    }

    &__icon {
        position: absolute;
        top: 0;
        left: -50px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 95px;
        background: $white;

        border: 2px solid $grey-10;
        border-radius: 360px;
        border-bottom-right-radius: 180px;
        aspect-ratio: 1/1;

        .svg-icon {
            color: $grey-40;
        }

        &-block {
            position: absolute;
            right: 12px;
            bottom: 12px;
            background: $white;
        }
    }

    &__content {
        @include pnp-typeface(text-16, no-margin);
    }
}

.personal-document-listing-loading {
    display: flex;
    flex-direction: column;

    &__label {
        @include pnp-visuallyhidden;
    }

    &__shadow {
        width: 100%;
        height: 30px;

        margin-bottom: 10px;
        background: $grey-20;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.personal-document-listing-sign-in {
    &__message {
        a {
            @include pnp-typeface(text-16, no-margin);
            @include pnp-link-text;
        }

        .google-api-auth,
        .microsoft-auth {
            margin-top: 5px;
        }
    }

    &__title {
        @include pnp-typeface(text-16-bold, no-margin);
        color: $grey-70;
    }
}
